*,
*::before,
*::after {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #6589F9;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.flip-card {
  background-color: transparent;
  width: 128px;
  height: 128px;
  -webkit-perspective: 800px;
          perspective: 800px;
}
.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  -webkit-transition: all 0.8s cubic-bezier(0.5, 1.3, 0.5, 1.3);
  transition: all 0.8s cubic-bezier(0.5, 1.3, 0.5, 1.3);
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}
.front, .back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}
.front {
  background-color: rgba(255, 255, 255, 0.3);
  z-index: 2;
}
.back {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  z-index: 1;
}
.front-img:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-filter: drop-shadow(0 0 0.4rem crimson);
          filter: drop-shadow(0 0 0.4rem crimson);
}
.selected, .match {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
.match {
  border: 1px solid salmon;
}
.grid {
  max-width: 900px;
  margin: 0 auto;

  display: grid;
  grid-template-columns: repeat(6, 128px);
  grid-template-rows: repeat(6, 128px);
  grid-gap: .5em;
}
